<template>
  <div class="linear" ref='linear' style='width: 380px;height: 300px;'>

  </div>
</template>

<script>
  export default{
    mounted(){
      let myChart = this.$echarts.init(this.$refs.linear);
      let option = {
          tooltip: {
              trigger: 'axis'
          },
          legend: {
              data: ['安全事件攻击数量'],
              textStyle: {
                    fontSize: 12,
                    color: '#fff'
                },
          },
          grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
          },
          xAxis: {
      		type: 'category',
      	    boundaryGap: false,//坐标轴两边留白
      		data: ['11.16', '11.17', '11.18','11.19','11.20','11.21','11.22'],
      		axisLabel: { //坐标轴刻度标签的相关设置。
      			interval: 0,//设置为 1，表示『隔一个标签显示一个标签』
      		//	margin:15,
      			textStyle: {
      				color: '#fff',
      				fontStyle: 'normal',
      				fontFamily: '微软雅黑',
      				fontSize: 12,
      			},
      			formatter:function(params) {
                      var newParamsName = "";
                      var paramsNameNumber = params.length;
                      var provideNumber = 5;  //一行显示几个字
                      var rowNumber = Math.ceil(paramsNameNumber / provideNumber);
                      if (paramsNameNumber > provideNumber) {
                          for (var p = 0; p < rowNumber; p++) {
                              var tempStr = "";
                              var start = p * provideNumber;
                              var end = start + provideNumber;
                              if (p == rowNumber - 1) {
                                  tempStr = params.substring(start, paramsNameNumber);
                              } else {
                                  tempStr = params.substring(start, end) + "\n";
                              }
                              newParamsName += tempStr;
                          }

                      } else {
                          newParamsName = params;
                      }
                      return newParamsName
                  },
      			//rotate:50,
      		},
      		axisTick:{//坐标轴刻度相关设置。
      			show: false,
      		},
      		axisLine:{//坐标轴轴线相关设置
      			lineStyle:{
      				color:'#E5E9ED',
      				// opacity:0.2
      			}
      		},
      		splitLine: { //坐标轴在 grid 区域中的分隔线。
      			show: false,
      			lineStyle: {
      				color: '#E5E9ED',
      			// 	opacity:0.1
      			}
      		}
          },
          yAxis: [
      		{
      			type: 'value',
      			splitNumber: 5,
      			axisLabel: {
      				textStyle: {
      					color: '#fff',
      					fontStyle: 'normal',
      					fontFamily: '微软雅黑',
      					fontSize: 12,
      				}
      			},
      			axisLine:{
      				show: false
      			},
      			axisTick:{
      				show: false
      			},
      			splitLine: {
      				show: false,
      				lineStyle: {
      					color: '#E5E9ED',
      				// 	opacity:0.1
      				}
      			}

      		}
      	],
          series: [
              {
                  name: '安全事件攻击数量',
                  type: 'line',
                  itemStyle: {
      		        normal: {
      					color:'#7538F5',
      		            lineStyle: {
      						color: "#7538F5",
      						width:1
      		            },
      		            areaStyle: {
      						color: new this.$echarts.graphic.LinearGradient(0, 1, 0, 0, [{
      							offset: 0,
      							color: 'rgba(117,56,245,0.3)'
      						}, {
      							offset: 1,
      							color: 'rgba(117,56,245,1)'
      						}]),
      		            }
      		        }
      			},
                  data: [210,101,170,50,101,50,140,0]
              }
          ]
      };

      myChart.setOption(option);


    }
  }
</script>

